@keyframes leftInputSlideOpen {
    0% {
        left: 72px;
    }
    100% {
        left: 0;
    }
}

@keyframes leftInputSlideClose {
    0% {
        left: 0;
    }
    100% {
        left: 25.5%;
    }
}

